import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'

const HeaderWrapper = styled.header`
  position: fixed;
  display: grid;
  left: 0;
  top: 0;
  padding: 0 20%;
  height: 75px;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #c9c9c9;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 75px;
  z-index: 100;
`

const BlogTitle = styled.h1`
  margin: 0;
  padding: 0;
  line-height: 75px;
  font-size: 20px;
  font-weight: 100;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  grid-template-rows: 75px;
  & > p {
    padding: 50px 0 0 10px;
  }
  & > a {
    height: 75px;
    display: flex;
    align-items: center;
  }
`

const HeaderLinks = styled.ul`
  list-style-type: none;
  margin: 0;
  display: grid;
  height: 75px;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  & > li {
    margin: 0;
    padding: 0;
    cursor: pointer;
    & > a {
      color: #4f4f4f;
      text-decoration: none;
      &:hover {
        font-weight: 900;
      }
    }
  }
`

const Header = () => {
  return (
    <HeaderWrapper>
      <BlogTitle>
        <Link to={'/'}>
          <img src='/logo.svg' />
        </Link>
        <p>기술 블로그</p>
      </BlogTitle>
      <HeaderLinks>
        <li>
          <a href='/about'>소개</a>
        </li>
        <li>
          <a href='/'>포스트</a>
        </li>
        <li>
          <a href='/recruits'>채용</a>
        </li>
      </HeaderLinks>
    </HeaderWrapper>
  )
}

export default Header
